"use client";

import { useState } from "react";


export default function TransactionModal({ onClose, onSubmit }) {
  const [form, setForm] = useState({
    date: "",
    description: "",
    deposit: "",
    withdrawal: "",
  });

  const updateField = (field, value) => {
    setForm((prev) => ({
      ...prev,
      [field]: value,
      ...(field === "deposit" ? { withdrawal: "" } : {}),
      ...(field === "withdrawal" ? { deposit: "" } : {}),
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const newTx = {
      date: form.date,
      description: form.description,
      deposit: Number(form.deposit) || 0,
      withdrawal: Number(form.withdrawal) || 0,
    };

    onSubmit(newTx);
    onClose();
  };

  return (
    <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
      <div className="bg-white rounded-xl shadow-xl w-full max-w-md p-6 animate-fadeIn">

        {/* HEADER */}
        <h2 className="text-xl font-semibold mb-4 text-emerald-700">
          ‚ûï Add New Transaction
        </h2>

        {/* FORM */}
        <form className="space-y-4" onSubmit={handleSubmit}>
          
          <div>
            <label className="block text-sm font-medium mb-1">Date</label>
            <input
              type="date"
              value={form.date}
              onChange={(e) => updateField("date", e.target.value)}
              required
              className="w-full border rounded-lg px-3 py-2"
            />
          </div>

          <div>
            <label className="block text-sm font-medium mb-1">Description</label>
            <input
              type="text"
              value={form.description}
              onChange={(e) => updateField("description", e.target.value)}
              required
              className="w-full border rounded-lg px-3 py-2"
            />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium mb-1">Deposit (‚Ç±)</label>
              <input
                type="number"
                value={form.deposit}
                onChange={(e) => updateField("deposit", e.target.value)}
                disabled={!!form.withdrawal}
                className="w-full border rounded-lg px-3 py-2"
              />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Withdrawal (‚Ç±)</label>
              <input
                type="number"
                value={form.withdrawal}
                onChange={(e) => updateField("withdrawal", e.target.value)}
                disabled={!!form.deposit}
                className="w-full border rounded-lg px-3 py-2"
              />
            </div>
          </div>

          {/* BUTTONS */}
          <div className="flex justify-end gap-3 pt-3">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 rounded-lg border text-slate-600 hover:bg-slate-100"
            >
              Cancel
            </button>

            <button
              type="submit"
              className="px-4 py-2 rounded-lg bg-emerald-600 text-white font-medium hover:bg-emerald-700 shadow"
            >
              Save Transaction
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
